// 跳首页
const jumpToFirst = (obj, fn) => {
  obj.page = 1
  return fn()
}

// 跳尾页
const jumpToLast = (obj, fn) => {
  const page = Math.ceil(obj.total / obj.size)
  obj.page = page
  return fn()
}


// 删除
const delData = (that, id) => {
  that.$confirm('此操作将删除数据, 是否继续?', '提示', { type: 'warning' })
    .then(() => {
      that.httpPost('/user/remove/' + id, {})
        .then(res => {
          that.loadData()
          that.$message({ type: 'success', message: '删除成功!' })
        })
        .catch(err => {
          console.log(err)
        })
    })
    .catch(() => { })
}


// 禁用
const disabledFn = (that, id, flag) => {
  var type = flag === 1 ? 'disable' : 'enable' // flag 1启用 0禁用
  that.$confirm(flag === 1 ? '是否确认禁用' : '是否确认启用' + '?', '提示', { type: 'warning' })
    .then(() => {
      that.httpPost('/user/' + type + '/' + id, {})
        .then(res => {
          that.loadData()
          that.$message({ type: 'success', message: flag === 1 ? '禁用成功' : '启用成功' })
        })
        .catch(err => {
          console.log(err)
        })
    })
    .catch(() => { })
}

// 深拷贝
const deepClone = (origin) => {
  if (!isObject(origin)) return origin
  var target = isArray(origin) ? [] : {}
  for (var prop in origin) {
    if (origin.hasOwnProperty(prop)) {
      var value = origin[prop]
      if (isObject(value)) {
        target[prop] = deepClone(value)
      } else {
        target[prop] = value
      }
      //if...else...可换成三目运算符
      //target[prop] = isObject(value) ? deepClone(value) : value
    }
  }
  function isObject(any) {
    return typeof any == 'object' && any != null
  }
  function isArray(any) {
    return Object.prototype.toString.call(any) === '[object Array]'
  }
  return target
}



export { jumpToFirst, jumpToLast, delData, disabledFn, deepClone }


