// 解决菜单重复点击报错
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

const router = new VueRouter({
  routes
})

// 路由判断登录 根据路由配置文件的参数
router.beforeEach((to, from, next) => {
  const that = new Vue()
  // debugger
  if (to.path === '/login') return next(), that.$sessionData('remove', 'USER'), false
  let requireAuth = to.matched.some(record => record.meta.requireAuth) // 该路由是否需要登录权限
  let getToken = that.$sessionData('get', 'USER') // 当前的token是否存在
  let url = { path: '/login' } //  没有token 登陆后要返回的地址

  requireAuth ? (getToken ? (to.path === '/login' ? that.$sessionData('remove', 'USER') : next()) : next(url)) : next()
})


export default router
