import 'babel-polyfill'
import Vue from 'vue'
import App from './App.vue'
import router from './router'

import Http from './http'

// 引入全局样式
import './assets/styles/global.scss'

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

import moment from 'moment'


// 视频
import VideoPlayer from 'vue-video-player'
import 'vue-video-player/src/custom-theme.css'
import 'video.js/dist/video-js.css'

// Base64
import base64 from 'js-base64'

import md5 from 'js-md5'

import { sessionData, getUser } from './utils/storage'
import { toBottom, toNext, toPrev, toTop } from './utils/sort'
import { jumpToFirst, jumpToLast, delData, disabledFn, deepClone } from './utils/util'

Vue.use(ElementUI)
Vue.use(Http)
Vue.use(VideoPlayer)

Vue.config.productionTip = false
Vue.prototype.$errorTip = function (msg) {
  this.$message({ duration: 2000, message: msg, type: 'error', offset: 200 })
}
Vue.prototype.$infoTip = function (msg) {
  this.$message({ duration: 2000, message: msg, type: 'info', offset: 200 })
}
const settings = require('@/settings')

Vue.prototype.$picUpload = settings.PictureUploadUrl  // 标准服务设置图片/网站配置图片 上传
Vue.prototype.$picDownload = settings.PictureDownloadUrl  // 标准服务设置图片/网站配置图片 下载

Vue.prototype.$videoUpload = settings.VideoUploadUrl  // 视频图片、文件 上传
Vue.prototype.$videoDownload = settings.VideoDownloadUrl  // 视频图片、文件 下载

Vue.prototype.$floatUpload = settings.FloatUploadUrl  // 悬浮窗图片、文件 上传

Vue.prototype.$adUpload = settings.AdUploadUrl  // 广告宣传、风采展示、文件 上传
Vue.prototype.$adDownload = settings.AdDownloadUrl  // 广告宣传、风采展示、文件 下载

Vue.prototype.$fileUpload = settings.FileUploadUrl  // 公告附件上传
Vue.prototype.$fileDownload = settings.FileDownloadUrl  // 公告附件下载

Vue.prototype.$ExcelUpload = settings.ExcelUploadUrl  // excel表格上传

Vue.prototype.$baseRequestUrl = settings.BZHBaseUrl

Vue.prototype.$webRequestUrl = settings.BZHWebBaseUrl

Vue.prototype.$templateDownloadUrl = settings.TemplateDownloadUrl

Vue.prototype.$wsUri = settings.WS_URI

Vue.prototype.$moment = moment

Vue.prototype.$base64 = base64.Base64

Vue.prototype.$md5 = md5

Vue.prototype.$sessionData = sessionData // this.$sessionData("方法","名字",数据); method：get获取，set存入或覆盖，remove清除
Vue.prototype.$getUser = getUser // 取user某一个key this.$getUser('storeId')  取整个user this.$getUser()
Vue.prototype.$toBottom = toBottom
Vue.prototype.$toNext = toNext
Vue.prototype.$toPrev = toPrev
Vue.prototype.$toTop = toTop

Vue.prototype.$deepClone = deepClone

Vue.prototype.$jumpToFirst = jumpToFirst
Vue.prototype.$jumpToLast = jumpToLast


Vue.prototype.$delData = delData
Vue.prototype.$disabledFn = disabledFn

Vue.prototype.$eventBus = new Vue()

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
